                p(data-aos="fade-down") MySQL Workbench ofrece la interfaz grfica con la que se pueden generar estos cdigos. Se mostrar cmo se hace con algunos ejemplos, siga los siguientes pasos:  

            .tarjeta.tarjeta--gris.p-4.mb-5(data-aos="fade-left")
              SlyderA(tipo="b")
                .row.justify-content-center.align-items-center.p-2
                  .col-lg-5.my-3.col-md-auto
                    h5 #[mark.bgtg.brad2.px-2 1]
                    .t5.pe-5
                      p Haga clic derecho a la entidad persona y luego a Copy to Clipboard (copiar SQL en portapapeles).  
                  .col-lg-5.my-3.mb-4.mb-md-0
                    img.img-a.img-t(src='@/assets/curso/temas/48.png', alt="")  
                 
                .row.justify-content-center.align-items-center.p-2
                  .col-lg-5.my-3.col-md-auto
                    h5 #[mark.bgtg.brad2.px-2 2]
                    .t5.pe-5
                      p Despus se debe pegar (Ctrl+V) el contenido en un editor de texto y el resultado ser: 
                  .col-lg-5.my-3.mb-4.mb-md-0
                    .bg20_1.p-4
                      img.img-t(src='@/assets/curso/temas/89.png', alt='')                    
                    .bg20.brad.p-4
                      p #[b CREATE TABLE IF NOT EXISTS] `mydb`.`persona` (
                      p.ps-3 `id_persona` #[b INT NOT NULL,]
                      p.ps-3 `identificacion` #[b VARCHAR(45) NOT NULL,]
                      p.ps-3 `nombres` #[b VARCHAR(450) NOT NULL,]
                      p.ps-3 `direccion` #[b VARCHAR(250) NULL,]
                      p.ps-3 `ciudad` #[b VARCHAR(80) NULL,]
                      p.ps-3 `codigo_postal` #[b VARCHAR(15) NULL,]
                      p.ps-3 `telefono` #[b VARCHAR(15) NULL,]
                      p.ps-3 `correo` #[b VARCHAR(250) NULL,]
                      p.ps-3 #[b PRIMARY KEY] (`id_persona`),
                      p.ps-3 #[b UNIQUE INDEX] `identificacion_#[b UNIQUE]` (`identificacion` #[b ASC]) #[b VISIBLE])
                      p.mb-0 ENGINE = InnoDB;

         
