                p(data-aos="fade-down") Con esta sintaxis, lo que hace MySQL #[i Workbench] es decirle al gestor de base de datos MySQL que esta columna id_persona debe guardarse en estructura de rbol binario (que se llame fk_factura_persona_idx) y no de manera secuencial.  Este tipo de estructura de datos en rbol mejora la velocidad de las operaciones de consulta y bsqueda.

                p(data-aos="fade-down") Tambin se crean ndices para las columnas id_compania e id_termino.
                .row.justify-content-center.align-items-center.px-3.bg3.brad(data-aos="flip-down")           
                  .col-lg-auto.beat.my-3
