                  p.ps-3 #[b REFERENCES] `mydb`.`persona` (`id_persona`)
                  p.mb-0.ps-3 #[b ON DELETE NO ACTION] ACTION,]
      

            h5(data-aos="fade-down").mb-5 A continuacin, se presenta lnea a lnea, recordando que la columna id_persona es una llave fornea de la tabla persona:

            .titulo-sexto.color-acento-contenido
              h5 Tabla 1.
              span Restricciones

            .tabla-a.color-acento-contenido.mb-5(data-aos="fade-left") 
              table
                thead.bgh    
                  tr
                    th.brth Nombre restriccin 
                    th.brth Descripcin 
                tbody.bgt
                  tr
                    td.brt #[b CONSTRAINT] `fk_factura_persona`   
                    td.brt Indica al SGDB que debe crear una restriccin (#[i CONSTRAINT]) que se llama fk_factura_persona 
                  tr
                    td.brt #[b FOREIGN KEY] (`id_persona`)
                    td.brt Indica la columna  que es llave fornea, es decir, que id_persona es una referencia de una fila de otra tabla.
                  tr
                    td.brt #[b REFERENCES] `mydb`.`persona` (`id_persona`)
                    td.brt La fila de la otra tabla de la que es referencia de la base de datos mydb, tabla persona columna id_persona
                  tr
                    td.brt #[b ON DELETE NO ACTION]
                    td.brt Qu accin debe hacer el SGDB si borran la fila de la tabla persona con los datos de la tabla factura que estn relacionados, ac dice que no le permite borrar personas que ya tienen facturas.
                  tr
                    td.brt #[b ON UPDATE NO ACTION,]
                    td.brt Qu accin debe hacer el SGDB si actualiza la columna id_persona una fila de la tabla persona con los datos de la tabla factura que estn relacionados, ac dice que no le permita actualizar la columna id_persona de la tabla persona que ya tiene facturas.      

            p(data-aos="fade-down") No es de preocuparse si no es muy claro hasta la decisin del SQL de llaves forneas, dado que a lo largo de la prctica y la revisin del material complementario, podra complementar los diferentes elementos y conceptos.       

